.futur__icon {
    margin-bottom: -8.8vw;
    font-size: 10.5vw;
    color: rgb(112, 190, 247);
}

.futur__container {
    position: relative;
    top: 0;
    width: auto;
    height: 455vw;
    background-image: 
    url("https://www.transparenttextures.com/patterns/cubes.png"),
    linear-gradient(rgb(14, 59, 90), rgb(25, 99, 148));
}

.futur__container__work {
    position: relative;
    width: auto;
    height: 50vw;
    margin-bottom: 20vw;
    background-color: white;
}

.futur__container__work__desktop {
    position: absolute;
    top: 10vw;
    left: 30vw;
    width: 60vw;
    height: auto;
}

.futur__logo-vowd {
    position: absolute;
	top: 14vw;
    left: 62vw;
    width: 18vw;
	height: auto;
    opacity: .35;
}

.futur__container__work__header {
    position: absolute;
    display: inline-block;
    text-align: left;
    width: 100%;
    height: auto;
    padding-left: 6vw;
    padding-top: 9vw;
    margin-top: 0;
    margin-bottom: 10vw;
    font-size: 9vw;
    font-weight: 700;
    line-height: 9vw;
    color: rgb(25, 99, 148);
}

.futur__container__content {
    width: auto;
    margin-left: 6vw;
    margin-right: 6vw;
    margin-bottom: 0vw;
    padding-bottom: 40vw;
    font-size: 4.8vw;
    font-weight: 300;
    line-height: 6vw;
    text-align: left;
    letter-spacing: unset;
    color: white;
}

.futur__container__content > li > span {
    font-weight: bold;
    color: white;
}

.pen {
    position: relative;
    left: 50%;
    margin-bottom: 3.5vw;
    transform: translate(-50%, 0);
    width: 18vw;
    height: auto;
}

.illustration {
    position: relative;
    left: 50%;
    margin-bottom: 2.5vw;
    transform: translate(-50%, 0);
    width: 15vw;
    height: auto;
}

.responsive {
    position: relative;
    left: 50%;
    margin-bottom: 3.2vw;
    transform: translate(-50%, 0);
    width: 14vw;
    height: auto;
    filter: brightness(120%);
}

.seo {
    position: relative;
    left: 50%;
    margin-bottom: 3vw;
    transform: translate(-50%, 0);
    width: 15vw;
    height: auto;
}

.hands {
    position: relative;
    left: 50%;
    margin-bottom: 3vw;
    transform: translate(-50%, 0);
    width: 28vw;
    height: auto;
}

/********************************************* media queries ************************************************/
/************************** New rules if the screen is at more 650px ****************************************/
@media screen and (min-width: 650px) {

    /**********************************************************************************************/
    /********************************************* futur ******************************************/
    /**********************************************************************************************/
    
    .futur__icon {
        margin-bottom: -31px;
        font-size: 40px;
    }
}