.services__icon {
    margin-bottom: -5.8vw;
    font-size: 9.7vw;
    color: rgb(112, 190, 247);
}

.services__container {
    position: relative;
    width: 100%;
    height: auto;

}

.services__container__picture {
    position: relative;
    object-fit: contain;
    width: 100%;
    height: auto;
    margin-bottom: -2vw;
    filter: brightness(65%);
}

.sevices__container--fixe-position {
    position: absolute;
    display: inline-block;
    margin-top: 10vw;
    z-index: 200;
}

.services__container__blocks {
    position: relative;
    display: flex;
    justify-content: center;
    margin-left: 4vw;
    width: 100%;
    height: auto;
    flex-direction: column;
}

.block {
    position: relative;
    width: 85vw;
    height: 35vw;
    margin-top: 10vw;
    padding-right: 5vw;
}

.block > h2 {
    position: absolute;
    font-family: 'Helvetica';
    top: -30vw;
    font-size: 25vw;
    font-weight: bold;
    text-transform: uppercase;
    background: radial-gradient(
        circle farthest-corner at center center,
        rgb(68, 172, 241),
        rgb(4, 92, 133)
    ) no-repeat;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  text-shadow: rgba(2, 143, 235, .2) .2vw 0 5vw;
  z-index: -1;
}

.block-paragraph {
    margin-left: 6.5vw;
    margin-top: -6.5vw;
    letter-spacing: .2vw;
    width: 100%;
}

.block-paragraph__header {
    line-height: 4.2vw;
    font-family: 'Helvetica';
    font-size: 4.8vw;
    font-weight: bold;
}

.block-paragraph__text {
    line-height: 3vw;
    font-family: 'Open' sans-serif;
    font-size: 4.5vw;
    font-weight: 500;
    line-height: 6vw;
    color: rgb(235, 233, 233);
}

.block-paragraph__text > span {
    font-weight: bolder;
    color: rgb(77, 233, 220);
}

/********************************************* media queries ************************************************/
/************************** New rules if the screen is at more 650px ****************************************/
@media screen and (min-width: 650px) {

    /**********************************************************************************************/
    /******************************************* services *****************************************/
    /**********************************************************************************************/

    .services__icon {
        margin-bottom: -20px;
        font-size: 40px;
    }
}