/**********************************************************************************************/
/********************************************* header *****************************************/
/**********************************************************************************************/

.navbar {
	position: relative;
    width: auto;
	background: linear-gradient(to top, rgb(97, 107, 114), 20%, transparent);
	transition: background-color 2s;
	z-index: 1;
}

.navbar-closed {
	height: 39.2vw;
	background-color: rgb(32, 53, 65);
	transition: height .3s, background-color .8s;
}

.navbar-open {
	height: 135vw;
	background-color:  rgba(50, 62, 70, 0.25);
	transition: height .4s, background-color 1.5s;
}

.navbar__logo-vowd {
	position: relative;
	top: 4.5vw;
	margin-left: 4.5vw;
	width: 12vw;
	height: auto;
	z-index: -1;
}

.navbar__title-vowd {
	position: relative;
	margin-left: -12vw;
	top: 11vw;
    font-size: 6vw;
    font-family: 'Play', sans-serif;
    font-weight: bold;
    letter-spacing: .5vw;
	text-transform: uppercase;
	color: rgb(231, 244, 253);
	z-index: 2;
}

.navbar__button__burger {
	position: relative;
    float: right;
    margin-top: 6vw;
	margin-right: 7.73vw;
	width: 10vw;
	height: auto;
	cursor: pointer;
	z-index: 500;
}

menu {
	position: relative;
	display: flex;
	flex-direction: column;
	top: 15vw;
	width: 87vw;
	overflow: hidden;
}

.navbar__menu {
	margin-left: -4.5vw;
}

.navbar__menu-open {
	height: 80vw;
	transition-delay: .2s;
	transition-duration: .25s;
}

.navbar__menu-closed {
	height: 0;
	transition-duration: .1s;
}

.nav__menu__link, a:link, a:visited, a:hover {
	text-decoration: none !important;
}

.nav__menu__link {
	position: relative;
	line-height: 10vw;
	border-bottom: 1px solid rgba(107, 177, 224, .4);
    padding-bottom: .5vw;
	cursor: pointer;
	letter-spacing: .3vw;
	font-size: 5.5vw;
	font-weight: 300;
	color: white;
	transition: all .3s;
}

.nav__menu__link:hover {
	border-bottom: 1px solid white;
	text-decoration: none;
	padding-left: 5vw;
	font-size: 5.9vw;
	font-weight: 400;
	color: white;
	transition: all .2s;
}

/********************************************* media queries ************************************************/
/************************** New rules if the screen is at more 552px ****************************************/
@media screen and (min-width: 553px) {

    /**********************************************************************************************/
    /********************************************* header *****************************************/
    /**********************************************************************************************/
	.navbar-closed {
		height: 120px;
	}
	
	.navbar-open {
		height: 640px;
	}

	.navbar__logo-vowd {
		top: 25px;
		margin-left: 45px;
		width: 60px;
	}

	.navbar__title-vowd {
		margin-left: 0;
		top: 0;
		font-size: 28px;
		letter-spacing: 2px;
	}
	
	.navbar__button__burger {
		margin-top: -20px;
		margin-right: 45px;
		width: 40px;
	}
	
	menu {
		top: 50px;
		width: 80vw;
	}
	
	.navbar__menu {
		margin-left: 5px;
	}
	
	.navbar__menu-open {
		height: 500px;
	}
	
	.nav__menu__link {
		line-height: 50px;
		padding-bottom: 5px;
		letter-spacing: 2px;
		font-size: 22px;
	}
	
	.nav__menu__link:hover {
		padding-left: 60px;
		font-size: 25px;
	}
    
}