.banner {
    position: relative;
    width: 100%;
    height: auto;
    background-color: black;
    padding-left: 6.5vw;
}

.banner__title{
    position: relative;
    top: 5vw;
    margin-bottom: 20vw;
    margin-left: -1.5vw;
	font-family: 'Play', sans-serif;
	font-weight: bold;
	font-size: 14.5vw;
	color: rgb(231, 244, 253);
    line-height: 15.5vw;
    letter-spacing: .52vw;
    z-index: 1000;
}

.banner__title > span {
	font-family: 'Open Sans', sans-serif;
	font-size: 28vw;
	font-weight: 700;
}

.banner__title-v {
    background: linear-gradient(to right, rgb(162, 215, 253), rgb(97, 164, 252));
    background-clip: text;
    -webkit-text-fill-color: transparent;
}

.banner__title-o {
    color: rgb(97, 164, 252);
}

.banner__title-v,
.banner__title-o {
    animation: reveal1 ease 5s normal forwards 1;
}

.banner__title-w {
    color: rgb(68, 148, 252);
    animation: reveal1 ease 8s normal forwards 1;
}

.banner__title-d {
    color: rgb(58, 126, 216);
    animation: reveal1 ease 9s normal forwards 1;
}

@keyframes reveal1 {
    0%{opacity: 0;}
    100%{opacity: 1;}
}

.banner__subtitle {
    position: relative;
    top: -10vw;
    margin-bottom: 8vw;
    width: 100%;
    font-size: 8.5vw;
    font-weight: 600;
    color:  rgb(153, 231, 255);
    animation-duration: 2.3s;
    animation-name: train;
    z-index: 1000;
}

@keyframes train {
    0%{margin-left: 100vw;}
    80%{margin-left: 100vw;}
    100%{margin-left: 0;}
}

.banner__text {
    top: -15vw;
    position: relative;
    margin-bottom: 17vw;
    width: 90vw;
    font-size: 4.5vw;
    font-weight: 400;
    line-height: 7vw;
    letter-spacing: .52vw;
    animation-delay: 1s ease 0s normal forwards 1;
    animation-duration: 8s;
    animation-name: reveal2;
    z-index: 1000;
}

@keyframes reveal2 {
    0%{opacity: 0;}
    35%{opacity: 0;}
    100%{opacity: 1;}
}

.banner__text > span {
    font-family: 'Open Sans', sans-serif;
    font-size: 4.5vw;
}

.banner__text > a {
    color: rgb(77, 233, 220);
    font-weight: 700;
    font-size: 5vw;
}

.banner__text__end {
    font-size: 5vw;
    font-weight: bold;
    color: white;
}

.banner__button {
    position: relative;
    align-items: center;
    display: flex;
    left: 17vw;
    bottom: 20vw;
    padding-left: 2vw;
    flex-wrap: nowrap;
    max-width: 53vw;
    width: auto;
    height: 15vw;
    border-radius: 12vw;
    background: linear-gradient(rgb(15, 59, 88), rgb(48, 98, 131));
    box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
    cursor: pointer;
    animation-delay: 2s ease 0s normal forwards 1;
    animation-duration: 10s;
    animation-name: reveal2;
    z-index: 100;
}

.banner__button:after {
    content: '';
    position: absolute;
    align-items: center;
    display: flex;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    border-radius: 12vw;
    background: linear-gradient(rgb(15, 59, 88), rgb(42, 85, 114));
    opacity: 0;
    transition: all .4s ease-in-out;
}

.banner__button:hover:after {
   opacity: 1;
   box-shadow: rgba(0, 0, 0, 0.16) 0px 10px 36px 0px, rgba(0, 0, 0, 0.06) 0px 0px 0px 1px;
}

.banner__button:hover .banner__button__btn,
.banner__button:hover .banner__button__text {
    color: rgb(77, 233, 220);
    transition: color .4s;
}

.banner__button__btn {
    position: relative;
    margin-right: 3vw;
    font-size: 11vw;
    color: rgb(231, 244, 253);
    transition: color .4s;
    z-index: 1;
}

.banner__button__text {
    position: relative;
    vertical-align: middle;
    font-family: 'Open-sans', sans-serif;
    font-size: 4.7vw;
    font-weight: bold;
    letter-spacing: .3vw;
    color: rgb(231, 244, 253);
    transition: color .4s;
    z-index: 1;
}

.banner__picture {
    position: absolute;
    top: 0;
    left: 0;
    object-fit: contain;
    width: 100%;
    height: auto;
    margin-bottom: -2vw;
    opacity: .7;
}

.banner__picture2 {
    display: none;
}

/********************************************* media queries ************************************************/
/************************** New rules if the screen is at more 650px ****************************************/
@media screen and (min-width: 650px) {

    /**********************************************************************************************/
    /********************************************* banner *****************************************/
    /**********************************************************************************************/
    
    .banner {
        padding-left: 50px;
        height: 100vw;
    }
    
    .banner__title{
        top: -10px;
        margin-bottom: 60px;
        margin-left: -5px;
        font-size: 60px;
        line-height: 68px;
        letter-spacing: 2px;
    }
    
    .banner__title > span {
        font-size: 120px;
    }
    
    .banner__subtitle {
        top: -50px;
        margin-bottom: 35px;
        font-size: 35px;
    }
    
    .banner__text {
        top: -65px;
        width: 100%;
        margin-bottom: -5vw;
        font-size: 18px;
        line-height: 32px;
        letter-spacing: 2px;
    }
    
    .banner__text > span {
        font-size: 20px;
    }
    
    .banner__text > a {
        font-size: 24px;
    }
    
    .banner__text__end {
        font-size: 24px;
    }
    
    .banner__button {
        position: absolute;
        left: 44vw;
        top: 142px;
        padding-left: 9px;
        width: 220px;
        height: 62px;
        border-radius: 30px;
        z-index: 1000;
    }
    
    .banner__button:after {
        border-radius: 30px;
    }
    
    .banner__button__btn {
        margin-right: 16px;
        font-size: 44px;
    }
    
    .banner__button__text {
        font-size: 20px;
        letter-spacing: 1px;
    }

    .banner__picture2 {
        position: absolute;
        display: block;
        top: 0;
        left: 0;
        width: auto;
        height: 100vw;
        margin-bottom: -2vw;
        opacity: .7;
    }

    .banner__picture {
        display: none;
    }
}