.footer {
	position: relative;
    width: 100%;
	height: auto;
    text-align: center;
	background-color: black;
}

.footer__fellow-me {
    margin-top: 10vw;
    margin-bottom: 20vw;
}

.footer__link {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    position: relative;
    top: -10vw;
    left: 50%;
    transform: translate(-50%, 0);
    width: 75vw;
    height: 20vw;
    text-align: center;
    align-items: center;
}

.footer__link__icon {
	position: relative;
    color: white;
    font-size: 9vw;
    opacity: .25;
    cursor: pointer;
    transition: all .2s;
}

.footer__link__icon:hover {
	color: rgb(112, 190, 247);
    font-size: 15vw;
    opacity: 1;
    transition: all .2s;
}

