.footer__logo-vowd {
    position: relative;
    margin-bottom: 2vw;
	top: 4.5vw;
    width: 18vw;
	height: auto;
}

.footer__logo > span {
    color: rgb(85, 85, 85);
}

.footer__website {
    position: relative;
    margin-bottom: 15vw;
    font-family: 'Open Sans', sans-serif;
	font-size: 4vw;
    font-weight: 200;
    color: rgb(192, 192, 192);
    text-decoration: none;
}

.footer__website > span {
    font-size: 6vw;
    font-family: 'Play', sans-serif;
    font-weight: bold;
    letter-spacing: .5vw;
	text-transform: uppercase;
    color: rgb(231, 244, 253);
}

.footer__container-info {
    position: relative;
    margin-bottom: -5vw;
    width: 100%;
    max-height: 76vw;
    font-family: 'Open', sans-serif;
    background-color: rgba(126, 126, 126, 0.4);
    overflow: hidden;
}

.picture-people {
    position: relative;
    width: 100%;
    height: auto;
    opacity: .4;
}

.footer__container-info__content{
    position: relative;
    top: -62vw;
    display: flex;
    flex-direction: column;
    width: 100%;
    height: auto;
}

.footer__container-info__li {
    width: 75vw;
    height: 12vw;
    text-align: left;
    margin-left: 15vw;
    font-weight: 100;
    letter-spacing: .1vw;
    color: white;
}

.footer__container-info__li__icon-info {
    font-size: 6.5vw;
}

.footer__container-info__li__text {
    font-size: 4.2vw;
    font-weight: 300;
    margin-top: -6vw;
    margin-left: 11vw;
    left: 11vw;
}

.footer__copyright {
    position: relative;
    width: auto;
    height: 10vw;
    font-size: 3.6vw;
    line-height: 10vw;
    z-index: 1000;
    background-color: rgb(32, 32, 32);
}